  import Detail from '../../public/detail'
  import Loading from '../../public/Loading'
  import Head from '../../public/head'
  import None from '../../public/noNumber'
  import loadingImg from '../../public/loadingImg'
  export default {
    name: "fenleiPage",
    data(){
      return {
        api: this.GLOBAL.baseURL,
        date: [],
        person: '',
        addClick: false,
        otherList: [
          {person: '成人+', adult: 0},
        ],
        imgUrl: '../../static/img/static/back.jpg',
        closeIndex:'',
        price: [0, 0],
        pickerOptions2: {
          disabledDate: (time) => {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        AllactiveList:[],
        KindId:'',
        total:0,
        searchList:[],
        left:0,
        isLoading:false,
        pageIndex:0,
        editSearch:'',
        content:'',
        clickSearch:false,
        tiyan:'',
        personNum:'',
        allprice:''
      }
    },
    components:{
      Head,
      Loading,
      None,
      loadingImg,
      Detail
    },
    watch:{
      content:function(){
        if(!this.content&&this.clickSearch){
          this.clickSearch = false
          this.goOn()
        }
      },
      date:function(){
        if(this.date){
          this.goOn()
        }else if(!this.allprice[1]&&!this.personNum&&!this.closeIndex&&!this.tiyan){
          this.clickSearch = false
          this.getAll(1)
        }else{
          this.goOn()
        }
      },
      personNum:function(){
        if(this.personNum){
          this.goOn()
        }else if(!this.allprice[1]&&!this.date&&!this.closeIndex&&!this.tiyan){
          this.clickSearch = false
          this.getAll(1)
        }else{
          this.goOn()
        }
      },
      allprice:function(){
        if(this.allprice){
          this.goOn()
        }else if(!this.personNum&&!this.date&&!this.closeIndex&&!this.tiyan){
          this.clickSearch = false
          this.getAll(1)
        }else{
          this.goOn()
        }
      },
      closeIndex:function(){
        if(this.closeIndex){
          this.goOn()
        }else if(!this.personNum&&!this.date&&!this.allprice&&!this.tiyan){
          this.clickSearch = false
          this.getAll(1)
        }else{
          this.goOn()
        }
      },
      tiyan:function(){
        if(this.tiyan){
          if(!this.content){
            this.editSearch = this.tiyan==1?'户外活动':this.tiyan == 2?'少数民族':'本土文化'
          }
          this.KindId  = this.tiyan
          this.goOn()
        }else if(!this.personNum&&!this.date&&!this.allprice&&!this.closeIndex){
          this.clickSearch = false
          this.getAll(1)
        }else{
          this.goOn()
        }
      }

    },
    methods:{
      chooseMoney(val){
        if(val == 1){
          this.personNum = this.person
        }else{
          this.allprice = this.price
        }
      },
      goOn(){
        this.getSearch(1,this.date?this.date[0]/1000:0,this.date?this.date[1]/1000:0,this.allprice[0],this.allprice[1],this.personNum,this.closeIndex)
      },
      getVal(msg){
        this.content = msg
      },
      prev(){
        if(this.pageIndex > 0){
          this.left += 240
          this.pageIndex --
        }
      },
      next(){
        if(this.pageIndex < this.searchList.length - 5){
          this.left -= 240
          this.pageIndex++
        }
      },
      toFenlei(item,index){
        this.$router.push({
          path: '/classification',
          query:{
            information: item.value,
            name: item.name
          }
        })
      },
      getKind(){
        this.$http.post(this.api + '/SubKindList',{
          token: localStorage.getItem('token'),
          top_id: this.KindId
        })
          .then(res=>{
            if(res.data.code == 1){
              let a = res.data.data
              var b = []
              for(let i=0;i<a.length;i++){
                b.push({img:a[i].image_id?a[i].domain+a[i].themb_url:this.imgUrl,name:a[i].kind_name,value:a[i].kind_id})
              }
              this.searchList = b
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getKind()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
/*      Xunhuan(a){
        for(let i = 0;i<a.length;i++){
          this.searchList.push({img:a[i].image_id?a[i].domain+a[i].themb_url:this.imgUrl,name:a[i].kind_name,value:a[i].kind_id})
          if(a[i].children){
            this.Xunhuan(a[i].children)
          }
        }
      },*/
      change(){
        if(this.closeIndex){
          this.closeIndex = ''
        }else{
          this.closeIndex = 1
        }
      },
      show(){
        this.$nextTick(() => {
          this.$refs.saveDateInput.focus()
        })
      },
      rejest(){
        this.price = [0,0]
      },
      reduce(item,index){
        if(index == 0){
          if(item.adult > 0){
            this.addClick = false
            item.adult = item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }
        }else{
          if(item.adult !=0){
            this.addClick = false
            item.adult=item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }else{
            item.adult = 0
          }
        }
      },
      getActive(){
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          kind_id: this.KindId ,
          sort:1,
          page: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.activeList = res.data.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getActive()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      add(item,index){
        if(this.person < 16){
          this.addClick = false
          item.adult = item.adult + 1
          let b =0
          for(let i = 0;i<this.otherList.length;i++){
            b = b + this.otherList[i].adult
          }
          this.person = b
        }else{
          this.addClick = true
        }

      },
      getAll(val){
        this.isLoading = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          kind_id: this.KindId ,
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.AllactiveList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.getAll(val)
            }else{
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
       this.goOn(val)
      },
      toPublish(item,index) {
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id
          }
        })
      },
      search(val){
        this.isLoading = true
        if(this.content){
          this.clickSearch = true
        }else{
          this.clickSearch = false
        }
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          keywords: this.content,
          kind_id:this.KindId,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.AllactiveList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.search(msg)
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      getSearch(val,data1,data2,price1,price2,person,volunteen){
        this.isLoading = true
        this.clickSearch = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          keywords: this.content,
          kind_id:this.KindId,
          page:val,
          activ_begin_time: data1,
          activ_end_time: data2,
          price_low:price1,
          price_high: price2,
          max_person_num:person,
          is_volunteen:volunteen
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.AllactiveList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.search(msg)
                })
            }else{
              alert(res.data.msg)
            }
          })
      }
    },
    mounted(){
      this.getKind()
      this.goOn()
    },
    created(){
      this.KindId = this.$route.query.information
      this.date = this.$route.query.date,
       this.personNum = this.$route.query.personNum,
        this.allprice = this.$route.query.allprice,
        this.closeIndex = this.$route.query.closeIndex,
        this.tiyan = this.$route.query.tiyan,
        this.content = this.$route.query.content,
        this.editSearch= this.$route.query.content
    }
  }



